.create {
    color: #555;
    width: 480px;
    margin: 40px auto;
  }
  .create form label input{
    height: 30px;
    width: 400px;
  }
  .create form label textarea{
    width: 400px;
    height: 40px;
  }
  .create label span {
    display: block;
    margin: 30px 0 10px;
  }
  .create p {
    margin-top: 0px;
    font-size: 0.8em;
  }
  .create button {
    display: block;
    width: 100px;
    font-size: 1em;
    color: #fff;
    padding: 8px 20px;
    border: 0;
    border-radius: 4px;
    background-color: #58249c;
    cursor: pointer;
    text-decoration: none;
    margin: 20px auto;
  }
  .ingredients {
   
    align-items: center;
  }
  .ingredients button {
    margin: 0 0 0 10px;
    margin-top: 10px;
 
  }
 